body {
  margin: 0;
  padding: 0;
  background-color: #F6F3EE;
}

.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  height: 1024px;
  background-color: #F6F3EE;
  padding-top: 0px;
}

.rectangle {
  width: 1380px;
  height: 800px;
  border: 5px solid #BB2B22;
  background-color: transparent;
  margin-bottom: 60px;
  position: relative;
  overflow: hidden; 
}

.image-container {
  display: flex;
  animation: slideImages 44s infinite linear; 
}

.image-slide {
  min-width: 1380px;
  height: 800px;
  object-fit: cover;
  flex-shrink: 0;
}


.image-container::after {
  content: "";
  display: flex;
  min-width: 0;
  height: 0;
}

@keyframes slideImages {
  0% {
    transform: translateX(0);
  }
  8% {
    transform: translateX(-100%);
  }
  16% {
    transform: translateX(-200%);
  }
  24% {
    transform: translateX(-300%);
  }
  32% {
    transform: translateX(-400%);
  }
  40% {
    transform: translateX(-500%);
  }
  48% {
    transform: translateX(-600%);
  }
  56% {
    transform: translateX(-700%);
  }
  64% {
    transform: translateX(-800%);
  }
  72% {
    transform: translateX(-900%);
  }
  80% {
    transform: translateX(-1000%);
  }
  88% {
    transform: translateX(-1100%);
  }
  96% {
    transform: translateX(-1200%);
  }
  100% {
    transform: translateX(-1200%);
  }
}

/* Для бесшовного перехода нужно в HTML дублировать первые несколько изображений в конце */
@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.main-title {
  font-family: 'Mak', sans-serif;
  font-weight: 300;
  font-size: 250px;
  color: #BB2B22;
  margin: 0;
  line-height: 1;
  animation: slideInFromLeft 3s ease-out;
}

.subtitle {
  font-family: 'Futura PT', sans-serif;
  font-size: 50px;
  color: #303030;
  margin-top: 24px;
  opacity: 0;
  animation: slideInFromLeft 3s ease-out 3s forwards;
}